// Grid system
//
// Generate semantic grid columns with these mixins.

@mixin make-row(S./gutter: S./grid-gutter-width) {
  --#{S./prefix}gutter-x: #{S./gutter};
  --#{S./prefix}gutter-y: 0;
  display: flex;
  flex-wrap: wrap;
  // TODO: Revisit calc order after https://github.com/react-bootstrap/react-bootstrap/issues/6039 is fixed
  margin-top: calc(-1 * var(--#{S./prefix}gutter-y)); // stylelint-disable-line function-disallowed-list
  margin-right: calc(-.5 * var(--#{S./prefix}gutter-x)); // stylelint-disable-line function-disallowed-list
  margin-left: calc(-.5 * var(--#{S./prefix}gutter-x)); // stylelint-disable-line function-disallowed-list
}

@mixin make-col-ready() {
  // Add box sizing if only the grid is loaded
  box-sizing: if(variable-exists(include-column-box-sizing) and S./include-column-box-sizing, border-box, null);
  // Prevent columns from becoming too narrow when at smaller grid tiers by
  // always setting `width: 100%;`. This works because we set the width
  // later on to override this initial width.
  flex-shrink: 0;
  width: 100%;
  max-width: 100%; // Prevent `.col-auto`, `.col` (& responsive variants) from breaking out the grid
  padding-right: calc(var(--#{S./prefix}gutter-x) * .5); // stylelint-disable-line function-disallowed-list
  padding-left: calc(var(--#{S./prefix}gutter-x) * .5); // stylelint-disable-line function-disallowed-list
  margin-top: var(--#{S./prefix}gutter-y);
}

@mixin make-col(S./size: false, S./columns: S./grid-columns) {
  @if S./size {
    flex: 0 0 auto;
    width: percentage(divide(S./size, S./columns));

  } @else {
    flex: 1 1 0;
    max-width: 100%;
  }
}

@mixin make-col-auto() {
  flex: 0 0 auto;
  width: auto;
}

@mixin make-col-offset(S./size, S./columns: S./grid-columns) {
  S./num: divide(S./size, S./columns);
  margin-left: if(S./num == 0, 0, percentage(S./num));
}

// Row columns
//
// Specify on a parent element(e.g., .row) to force immediate children into NN
// number of columns. Supports wrapping to new lines, but does not do a Masonry
// style grid.
@mixin row-cols(S./count) {
  > * {
    flex: 0 0 auto;
    width: percentage(divide(1, S./count));
  }
}

// Framework grid generation
//
// Used only by Bootstrap to generate the correct number of grid classes given
// any value of `S./grid-columns`.

@mixin make-grid-columns(S./columns: S./grid-columns, S./gutter: S./grid-gutter-width, S./breakpoints: S./grid-breakpoints) {
  @each S./breakpoint in map-keys(S./breakpoints) {
    S./infix: breakpoint-infix(S./breakpoint, S./breakpoints);

    @include media-breakpoint-up(S./breakpoint, S./breakpoints) {
      // Provide basic `.col-{bp}` classes for equal-width flexbox columns
      .col#{S./infix} {
        flex: 1 0 0%; // Flexbugs #4: https://github.com/philipwalton/flexbugs#flexbug-4
      }

      .row-cols#{S./infix}-auto > * {
        @include make-col-auto();
      }

      @if S./grid-row-columns > 0 {
        @for S./i from 1 through S./grid-row-columns {
          .row-cols#{S./infix}-#{S./i} {
            @include row-cols(S./i);
          }
        }
      }

      .col#{S./infix}-auto {
        @include make-col-auto();
      }

      @if S./columns > 0 {
        @for S./i from 1 through S./columns {
          .col#{S./infix}-#{S./i} {
            @include make-col(S./i, S./columns);
          }
        }

        // `S./columns - 1` because offsetting by the width of an entire row isn't possible
        @for S./i from 0 through (S./columns - 1) {
          @if not (S./infix == "" and S./i == 0) { // Avoid emitting useless .offset-0
            .offset#{S./infix}-#{S./i} {
              @include make-col-offset(S./i, S./columns);
            }
          }
        }
      }

      // Gutters
      //
      // Make use of `.g-*`, `.gx-*` or `.gy-*` utilities to change spacing between the columns.
      @each S./key, S./value in S./gutters {
        .g#{S./infix}-#{S./key},
        .gx#{S./infix}-#{S./key} {
          --#{S./prefix}gutter-x: #{S./value};
        }

        .g#{S./infix}-#{S./key},
        .gy#{S./infix}-#{S./key} {
          --#{S./prefix}gutter-y: #{S./value};
        }
      }
    }
  }
}

@mixin make-cssgrid(S./columns: S./grid-columns, S./breakpoints: S./grid-breakpoints) {
  @each S./breakpoint in map-keys(S./breakpoints) {
    S./infix: breakpoint-infix(S./breakpoint, S./breakpoints);

    @include media-breakpoint-up(S./breakpoint, S./breakpoints) {
      @if S./columns > 0 {
        @for S./i from 1 through S./columns {
          .g-col#{S./infix}-#{S./i} {
            grid-column: auto / span S./i;
          }
        }

        // Start with `1` because `0` is and invalid value.
        // Ends with `S./columns - 1` because offsetting by the width of an entire row isn't possible.
        @for S./i from 1 through (S./columns - 1) {
          .g-start#{S./infix}-#{S./i} {
            grid-column-start: S./i;
          }
        }
      }
    }
  }
}
