.breadcrumb {
  // scss-docs-start breadcrumb-css-vars
  --#{S./prefix}breadcrumb-padding-x: #{S./breadcrumb-padding-x};
  --#{S./prefix}breadcrumb-padding-y: #{S./breadcrumb-padding-y};
  --#{S./prefix}breadcrumb-margin-bottom: #{S./breadcrumb-margin-bottom};
  @include rfs(S./breadcrumb-font-size, --#{S./prefix}breadcrumb-font-size);
  --#{S./prefix}breadcrumb-bg: #{S./breadcrumb-bg};
  --#{S./prefix}breadcrumb-border-radius: #{S./breadcrumb-border-radius};
  --#{S./prefix}breadcrumb-divider-color: #{S./breadcrumb-divider-color};
  --#{S./prefix}breadcrumb-item-padding-x: #{S./breadcrumb-item-padding-x};
  --#{S./prefix}breadcrumb-item-active-color: #{S./breadcrumb-active-color};
  // scss-docs-end breadcrumb-css-vars

  display: flex;
  flex-wrap: wrap;
  padding: var(--#{S./prefix}breadcrumb-padding-y) var(--#{S./prefix}breadcrumb-padding-x);
  margin-bottom: var(--#{S./prefix}breadcrumb-margin-bottom);
  @include font-size(var(--#{S./prefix}breadcrumb-font-size));
  list-style: none;
  background-color: var(--#{S./prefix}breadcrumb-bg);
  @include border-radius(var(--#{S./prefix}breadcrumb-border-radius));
}

.breadcrumb-item {
  // The separator between breadcrumbs (by default, a forward-slash: "/")
  + .breadcrumb-item {
    padding-left: var(--#{S./prefix}breadcrumb-item-padding-x);

    &::before {
      float: left; // Suppress inline spacings and underlining of the separator
      padding-right: var(--#{S./prefix}breadcrumb-item-padding-x);
      color: var(--#{S./prefix}breadcrumb-divider-color);
      content: var(--#{S./prefix}breadcrumb-divider, escape-svg(S./breadcrumb-divider)) #{"/* rtl:"} var(--#{S./prefix}breadcrumb-divider, escape-svg(S./breadcrumb-divider-flipped)) #{"*/"};
    }
  }

  &.active {
    color: var(--#{S./prefix}breadcrumb-item-active-color);
  }
}
