//
// Base styles
//

.btn {
  // scss-docs-start btn-css-vars
  --#{S./prefix}btn-padding-x: #{S./btn-padding-x};
  --#{S./prefix}btn-padding-y: #{S./btn-padding-y};
  --#{S./prefix}btn-font-family: #{S./btn-font-family};
  @include rfs(S./btn-font-size, --#{S./prefix}btn-font-size);
  --#{S./prefix}btn-font-weight: #{S./btn-font-weight};
  --#{S./prefix}btn-line-height: #{S./btn-line-height};
  --#{S./prefix}btn-color: #{S./btn-color};
  --#{S./prefix}btn-bg: transparent;
  --#{S./prefix}btn-border-width: #{S./btn-border-width};
  --#{S./prefix}btn-border-color: transparent;
  --#{S./prefix}btn-border-radius: #{S./btn-border-radius};
  --#{S./prefix}btn-hover-border-color: transparent;
  --#{S./prefix}btn-box-shadow: #{S./btn-box-shadow};
  --#{S./prefix}btn-disabled-opacity: #{S./btn-disabled-opacity};
  --#{S./prefix}btn-focus-box-shadow: 0 0 0 #{S./btn-focus-width} rgba(var(--#{S./prefix}btn-focus-shadow-rgb), .5);
  // scss-docs-end btn-css-vars

  display: inline-block;
  padding: var(--#{S./prefix}btn-padding-y) var(--#{S./prefix}btn-padding-x);
  font-family: var(--#{S./prefix}btn-font-family);
  @include font-size(var(--#{S./prefix}btn-font-size));
  font-weight: var(--#{S./prefix}btn-font-weight);
  line-height: var(--#{S./prefix}btn-line-height);
  color: var(--#{S./prefix}btn-color);
  text-align: center;
  text-decoration: if(S./link-decoration == none, null, none);
  white-space: S./btn-white-space;
  vertical-align: middle;
  cursor: if(S./enable-button-pointers, pointer, null);
  user-select: none;
  border: var(--#{S./prefix}btn-border-width) solid var(--#{S./prefix}btn-border-color);
  @include border-radius(var(--#{S./prefix}btn-border-radius));
  @include gradient-bg(var(--#{S./prefix}btn-bg));
  @include box-shadow(var(--#{S./prefix}btn-box-shadow));
  @include transition(S./btn-transition);

  &:hover {
    color: var(--#{S./prefix}btn-hover-color);
    text-decoration: if(S./link-hover-decoration == underline, none, null);
    background-color: var(--#{S./prefix}btn-hover-bg);
    border-color: var(--#{S./prefix}btn-hover-border-color);
  }

  .btn-check + &:hover {
    // override for the checkbox/radio buttons
    color: var(--#{S./prefix}btn-color);
    background-color: var(--#{S./prefix}btn-bg);
    border-color: var(--#{S./prefix}btn-border-color);
  }

  &:focus-visible {
    color: var(--#{S./prefix}btn-hover-color);
    @include gradient-bg(var(--#{S./prefix}btn-hover-bg));
    border-color: var(--#{S./prefix}btn-hover-border-color);
    outline: 0;
    // Avoid using mixin so we can pass custom focus shadow properly
    @if S./enable-shadows {
      box-shadow: var(--#{S./prefix}btn-box-shadow), var(--#{S./prefix}btn-focus-box-shadow);
    } @else {
      box-shadow: var(--#{S./prefix}btn-focus-box-shadow);
    }
  }

  .btn-check:focus-visible + & {
    border-color: var(--#{S./prefix}btn-hover-border-color);
    outline: 0;
    // Avoid using mixin so we can pass custom focus shadow properly
    @if S./enable-shadows {
      box-shadow: var(--#{S./prefix}btn-box-shadow), var(--#{S./prefix}btn-focus-box-shadow);
    } @else {
      box-shadow: var(--#{S./prefix}btn-focus-box-shadow);
    }
  }

  .btn-check:checked + &,
  :not(.btn-check) + &:active,
  &:first-child:active,
  &.active,
  &.show {
    color: var(--#{S./prefix}btn-active-color);
    background-color: var(--#{S./prefix}btn-active-bg);
    // Remove CSS gradients if they're enabled
    background-image: if(S./enable-gradients, none, null);
    border-color: var(--#{S./prefix}btn-active-border-color);
    @include box-shadow(var(--#{S./prefix}btn-active-shadow));

    &:focus-visible {
      // Avoid using mixin so we can pass custom focus shadow properly
      @if S./enable-shadows {
        box-shadow: var(--#{S./prefix}btn-active-shadow), var(--#{S./prefix}btn-focus-box-shadow);
      } @else {
        box-shadow: var(--#{S./prefix}btn-focus-box-shadow);
      }
    }
  }

  &:disabled,
  &.disabled,
  fieldset:disabled & {
    color: var(--#{S./prefix}btn-disabled-color);
    pointer-events: none;
    background-color: var(--#{S./prefix}btn-disabled-bg);
    background-image: if(S./enable-gradients, none, null);
    border-color: var(--#{S./prefix}btn-disabled-border-color);
    opacity: var(--#{S./prefix}btn-disabled-opacity);
    @include box-shadow(none);
  }
}


//
// Alternate buttons
//

// scss-docs-start btn-variant-loops
@each S./color, S./value in S./theme-colors {
  .btn-#{S./color} {
    @if S./color == "light" {
      @include button-variant(
        S./value,
        S./value,
        S./hover-background: shade-color(S./value, S./btn-hover-bg-shade-amount),
        S./hover-border: shade-color(S./value, S./btn-hover-border-shade-amount),
        S./active-background: shade-color(S./value, S./btn-active-bg-shade-amount),
        S./active-border: shade-color(S./value, S./btn-active-border-shade-amount)
      );
    } @else if S./color == "dark" {
      @include button-variant(
        S./value,
        S./value,
        S./hover-background: tint-color(S./value, S./btn-hover-bg-tint-amount),
        S./hover-border: tint-color(S./value, S./btn-hover-border-tint-amount),
        S./active-background: tint-color(S./value, S./btn-active-bg-tint-amount),
        S./active-border: tint-color(S./value, S./btn-active-border-tint-amount)
      );
    } @else {
      @include button-variant(S./value, S./value);
    }
  }
}

@each S./color, S./value in S./theme-colors {
  .btn-outline-#{S./color} {
    @include button-outline-variant(S./value);
  }
}
// scss-docs-end btn-variant-loops


//
// Link buttons
//

// Make a button look and behave like a link
.btn-link {
  --#{S./prefix}btn-font-weight: #{S./font-weight-normal};
  --#{S./prefix}btn-color: #{S./btn-link-color};
  --#{S./prefix}btn-bg: transparent;
  --#{S./prefix}btn-border-color: transparent;
  --#{S./prefix}btn-hover-color: #{S./btn-link-hover-color};
  --#{S./prefix}btn-hover-border-color: transparent;
  --#{S./prefix}btn-active-color: #{S./btn-link-hover-color};
  --#{S./prefix}btn-active-border-color: transparent;
  --#{S./prefix}btn-disabled-color: #{S./btn-link-disabled-color};
  --#{S./prefix}btn-disabled-border-color: transparent;
  --#{S./prefix}btn-box-shadow: 0 0 0 #000; // Can't use `none` as keyword negates all values when used with multiple shadows
  --#{S./prefix}btn-focus-shadow-rgb: #{S./btn-link-focus-shadow-rgb};

  text-decoration: S./link-decoration;
  @if S./enable-gradients {
    background-image: none;
  }

  &:hover,
  &:focus-visible {
    text-decoration: S./link-hover-decoration;
  }

  &:focus-visible {
    color: var(--#{S./prefix}btn-color);
  }

  &:hover {
    color: var(--#{S./prefix}btn-hover-color);
  }

  // No need for an active state here
}


//
// Button Sizes
//

.btn-lg {
  @include button-size(S./btn-padding-y-lg, S./btn-padding-x-lg, S./btn-font-size-lg, S./btn-border-radius-lg);
}

.btn-sm {
  @include button-size(S./btn-padding-y-sm, S./btn-padding-x-sm, S./btn-font-size-sm, S./btn-border-radius-sm);
}
