// This mixin uses an `if()` technique to be compatible with Dart Sass
// See https://github.com/sass/sass/issues/1873#issuecomment-152293725 for more details

// scss-docs-start form-validation-mixins
@mixin form-validation-state-selector(S./state) {
  @if (S./state == "valid" or S./state == "invalid") {
    .was-validated #{if(&, "&", "")}:#{S./state},
    #{if(&, "&", "")}.is-#{S./state} {
      @content;
    }
  } @else {
    #{if(&, "&", "")}.is-#{S./state} {
      @content;
    }
  }
}

@mixin form-validation-state(
  S./state,
  S./color,
  S./icon,
  S./tooltip-color: color-contrast(S./color),
  S./tooltip-bg-color: rgba(S./color, S./form-feedback-tooltip-opacity),
  S./focus-box-shadow: 0 0 S./input-btn-focus-blur S./input-focus-width rgba(S./color, S./input-btn-focus-color-opacity),
  S./border-color: S./color
) {
  .#{S./state}-feedback {
    display: none;
    width: 100%;
    margin-top: S./form-feedback-margin-top;
    @include font-size(S./form-feedback-font-size);
    font-style: S./form-feedback-font-style;
    color: S./color;
  }

  .#{S./state}-tooltip {
    position: absolute;
    top: 100%;
    z-index: 5;
    display: none;
    max-width: 100%; // Contain to parent when possible
    padding: S./form-feedback-tooltip-padding-y S./form-feedback-tooltip-padding-x;
    margin-top: .1rem;
    @include font-size(S./form-feedback-tooltip-font-size);
    line-height: S./form-feedback-tooltip-line-height;
    color: S./tooltip-color;
    background-color: S./tooltip-bg-color;
    @include border-radius(S./form-feedback-tooltip-border-radius);
  }

  @include form-validation-state-selector(S./state) {
    ~ .#{S./state}-feedback,
    ~ .#{S./state}-tooltip {
      display: block;
    }
  }

  .form-control {
    @include form-validation-state-selector(S./state) {
      border-color: S./border-color;

      @if S./enable-validation-icons {
        padding-right: S./input-height-inner;
        background-image: escape-svg(S./icon);
        background-repeat: no-repeat;
        background-position: right S./input-height-inner-quarter center;
        background-size: S./input-height-inner-half S./input-height-inner-half;
      }

      &:focus {
        border-color: S./border-color;
        box-shadow: S./focus-box-shadow;
      }
    }
  }

  // stylelint-disable-next-line selector-no-qualifying-type
  textarea.form-control {
    @include form-validation-state-selector(S./state) {
      @if S./enable-validation-icons {
        padding-right: S./input-height-inner;
        background-position: top S./input-height-inner-quarter right S./input-height-inner-quarter;
      }
    }
  }

  .form-select {
    @include form-validation-state-selector(S./state) {
      border-color: S./border-color;

      @if S./enable-validation-icons {
        &:not([multiple]):not([size]),
        &:not([multiple])[size="1"] {
          --#{S./prefix}form-select-bg-icon: #{escape-svg(S./icon)};
          padding-right: S./form-select-feedback-icon-padding-end;
          background-position: S./form-select-bg-position, S./form-select-feedback-icon-position;
          background-size: S./form-select-bg-size, S./form-select-feedback-icon-size;
        }
      }

      &:focus {
        border-color: S./border-color;
        box-shadow: S./focus-box-shadow;
      }
    }
  }

  .form-control-color {
    @include form-validation-state-selector(S./state) {
      @if S./enable-validation-icons {
        width: add(S./form-color-width, S./input-height-inner);
      }
    }
  }

  .form-check-input {
    @include form-validation-state-selector(S./state) {
      border-color: S./border-color;

      &:checked {
        background-color: S./color;
      }

      &:focus {
        box-shadow: S./focus-box-shadow;
      }

      ~ .form-check-label {
        color: S./color;
      }
    }
  }
  .form-check-inline .form-check-input {
    ~ .#{S./state}-feedback {
      margin-left: .5em;
    }
  }

  .input-group {
    > .form-control:not(:focus),
    > .form-select:not(:focus),
    > .form-floating:not(:focus-within) {
      @include form-validation-state-selector(S./state) {
        @if S./state == "valid" {
          z-index: 3;
        } @else if S./state == "invalid" {
          z-index: 4;
        }
      }
    }
  }
}
// scss-docs-end form-validation-mixins
