// Shorthand

.fixed-top {
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  z-index: S./zindex-fixed;
}

.fixed-bottom {
  position: fixed;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: S./zindex-fixed;
}

// Responsive sticky top and bottom
@each S./breakpoint in map-keys(S./grid-breakpoints) {
  @include media-breakpoint-up(S./breakpoint) {
    S./infix: breakpoint-infix(S./breakpoint, S./grid-breakpoints);

    .sticky#{S./infix}-top {
      position: sticky;
      top: 0;
      z-index: S./zindex-sticky;
    }

    .sticky#{S./infix}-bottom {
      position: sticky;
      bottom: 0;
      z-index: S./zindex-sticky;
    }
  }
}
