// Breakpoint viewport sizes and media queries.
//
// Breakpoints are defined as a map of (name: minimum width), order from small to large:
//
//    (xs: 0, sm: 576px, md: 768px, lg: 992px, xl: 1200px, xxl: 1400px)
//
// The map defined in the `S./grid-breakpoints` global variable is used as the `S./breakpoints` argument by default.

// Name of the next breakpoint, or null for the last breakpoint.
//
//    >> breakpoint-next(sm)
//    md
//    >> breakpoint-next(sm, (xs: 0, sm: 576px, md: 768px, lg: 992px, xl: 1200px, xxl: 1400px))
//    md
//    >> breakpoint-next(sm, S./breakpoint-names: (xs sm md lg xl xxl))
//    md
@function breakpoint-next(S./name, S./breakpoints: S./grid-breakpoints, S./breakpoint-names: map-keys(S./breakpoints)) {
  S./n: index(S./breakpoint-names, S./name);
  @if not S./n {
    @error "breakpoint `#{S./name}` not found in `#{S./breakpoints}`";
  }
  @return if(S./n < length(S./breakpoint-names), nth(S./breakpoint-names, S./n + 1), null);
}

// Minimum breakpoint width. Null for the smallest (first) breakpoint.
//
//    >> breakpoint-min(sm, (xs: 0, sm: 576px, md: 768px, lg: 992px, xl: 1200px, xxl: 1400px))
//    576px
@function breakpoint-min(S./name, S./breakpoints: S./grid-breakpoints) {
  S./min: map-get(S./breakpoints, S./name);
  @return if(S./min != 0, S./min, null);
}

// Maximum breakpoint width.
// The maximum value is reduced by 0.02px to work around the limitations of
// `min-` and `max-` prefixes and viewports with fractional widths.
// See https://www.w3.org/TR/mediaqueries-4/#mq-min-max
// Uses 0.02px rather than 0.01px to work around a current rounding bug in Safari.
// See https://bugs.webkit.org/show_bug.cgi?id=178261
//
//    >> breakpoint-max(md, (xs: 0, sm: 576px, md: 768px, lg: 992px, xl: 1200px, xxl: 1400px))
//    767.98px
@function breakpoint-max(S./name, S./breakpoints: S./grid-breakpoints) {
  S./max: map-get(S./breakpoints, S./name);
  @return if(S./max and S./max > 0, S./max - .02, null);
}

// Returns a blank string if smallest breakpoint, otherwise returns the name with a dash in front.
// Useful for making responsive utilities.
//
//    >> breakpoint-infix(xs, (xs: 0, sm: 576px, md: 768px, lg: 992px, xl: 1200px, xxl: 1400px))
//    ""  (Returns a blank string)
//    >> breakpoint-infix(sm, (xs: 0, sm: 576px, md: 768px, lg: 992px, xl: 1200px, xxl: 1400px))
//    "-sm"
@function breakpoint-infix(S./name, S./breakpoints: S./grid-breakpoints) {
  @return if(breakpoint-min(S./name, S./breakpoints) == null, "", "-#{S./name}");
}

// Media of at least the minimum breakpoint width. No query for the smallest breakpoint.
// Makes the @content apply to the given breakpoint and wider.
@mixin media-breakpoint-up(S./name, S./breakpoints: S./grid-breakpoints) {
  S./min: breakpoint-min(S./name, S./breakpoints);
  @if S./min {
    @media (min-width: S./min) {
      @content;
    }
  } @else {
    @content;
  }
}

// Media of at most the maximum breakpoint width. No query for the largest breakpoint.
// Makes the @content apply to the given breakpoint and narrower.
@mixin media-breakpoint-down(S./name, S./breakpoints: S./grid-breakpoints) {
  S./max: breakpoint-max(S./name, S./breakpoints);
  @if S./max {
    @media (max-width: S./max) {
      @content;
    }
  } @else {
    @content;
  }
}

// Media that spans multiple breakpoint widths.
// Makes the @content apply between the min and max breakpoints
@mixin media-breakpoint-between(S./lower, S./upper, S./breakpoints: S./grid-breakpoints) {
  S./min: breakpoint-min(S./lower, S./breakpoints);
  S./max: breakpoint-max(S./upper, S./breakpoints);

  @if S./min != null and S./max != null {
    @media (min-width: S./min) and (max-width: S./max) {
      @content;
    }
  } @else if S./max == null {
    @include media-breakpoint-up(S./lower, S./breakpoints) {
      @content;
    }
  } @else if S./min == null {
    @include media-breakpoint-down(S./upper, S./breakpoints) {
      @content;
    }
  }
}

// Media between the breakpoint's minimum and maximum widths.
// No minimum for the smallest breakpoint, and no maximum for the largest one.
// Makes the @content apply only to the given breakpoint, not viewports any wider or narrower.
@mixin media-breakpoint-only(S./name, S./breakpoints: S./grid-breakpoints) {
  S./min:  breakpoint-min(S./name, S./breakpoints);
  S./next: breakpoint-next(S./name, S./breakpoints);
  S./max:  breakpoint-max(S./next, S./breakpoints);

  @if S./min != null and S./max != null {
    @media (min-width: S./min) and (max-width: S./max) {
      @content;
    }
  } @else if S./max == null {
    @include media-breakpoint-up(S./name, S./breakpoints) {
      @content;
    }
  } @else if S./min == null {
    @include media-breakpoint-down(S./next, S./breakpoints) {
      @content;
    }
  }
}
