// Button variants
//
// Easily pump out default styles, as well as :hover, :focus, :active,
// and disabled options for all buttons

// scss-docs-start btn-variant-mixin
@mixin button-variant(
  S./background,
  S./border,
  S./color: color-contrast(S./background),
  S./hover-background: if(S./color == S./color-contrast-light, shade-color(S./background, S./btn-hover-bg-shade-amount), tint-color(S./background, S./btn-hover-bg-tint-amount)),
  S./hover-border: if(S./color == S./color-contrast-light, shade-color(S./border, S./btn-hover-border-shade-amount), tint-color(S./border, S./btn-hover-border-tint-amount)),
  S./hover-color: color-contrast(S./hover-background),
  S./active-background: if(S./color == S./color-contrast-light, shade-color(S./background, S./btn-active-bg-shade-amount), tint-color(S./background, S./btn-active-bg-tint-amount)),
  S./active-border: if(S./color == S./color-contrast-light, shade-color(S./border, S./btn-active-border-shade-amount), tint-color(S./border, S./btn-active-border-tint-amount)),
  S./active-color: color-contrast(S./active-background),
  S./disabled-background: S./background,
  S./disabled-border: S./border,
  S./disabled-color: color-contrast(S./disabled-background)
) {
  --#{S./prefix}btn-color: #{S./color};
  --#{S./prefix}btn-bg: #{S./background};
  --#{S./prefix}btn-border-color: #{S./border};
  --#{S./prefix}btn-hover-color: #{S./hover-color};
  --#{S./prefix}btn-hover-bg: #{S./hover-background};
  --#{S./prefix}btn-hover-border-color: #{S./hover-border};
  --#{S./prefix}btn-focus-shadow-rgb: #{to-rgb(mix(S./color, S./border, 15%))};
  --#{S./prefix}btn-active-color: #{S./active-color};
  --#{S./prefix}btn-active-bg: #{S./active-background};
  --#{S./prefix}btn-active-border-color: #{S./active-border};
  --#{S./prefix}btn-active-shadow: #{S./btn-active-box-shadow};
  --#{S./prefix}btn-disabled-color: #{S./disabled-color};
  --#{S./prefix}btn-disabled-bg: #{S./disabled-background};
  --#{S./prefix}btn-disabled-border-color: #{S./disabled-border};
}
// scss-docs-end btn-variant-mixin

// scss-docs-start btn-outline-variant-mixin
@mixin button-outline-variant(
  S./color,
  S./color-hover: color-contrast(S./color),
  S./active-background: S./color,
  S./active-border: S./color,
  S./active-color: color-contrast(S./active-background)
) {
  --#{S./prefix}btn-color: #{S./color};
  --#{S./prefix}btn-border-color: #{S./color};
  --#{S./prefix}btn-hover-color: #{S./color-hover};
  --#{S./prefix}btn-hover-bg: #{S./active-background};
  --#{S./prefix}btn-hover-border-color: #{S./active-border};
  --#{S./prefix}btn-focus-shadow-rgb: #{to-rgb(S./color)};
  --#{S./prefix}btn-active-color: #{S./active-color};
  --#{S./prefix}btn-active-bg: #{S./active-background};
  --#{S./prefix}btn-active-border-color: #{S./active-border};
  --#{S./prefix}btn-active-shadow: #{S./btn-active-box-shadow};
  --#{S./prefix}btn-disabled-color: #{S./color};
  --#{S./prefix}btn-disabled-bg: transparent;
  --#{S./prefix}btn-disabled-border-color: #{S./color};
  --#{S./prefix}gradient: none;
}
// scss-docs-end btn-outline-variant-mixin

// scss-docs-start btn-size-mixin
@mixin button-size(S./padding-y, S./padding-x, S./font-size, S./border-radius) {
  --#{S./prefix}btn-padding-y: #{S./padding-y};
  --#{S./prefix}btn-padding-x: #{S./padding-x};
  @include rfs(S./font-size, --#{S./prefix}btn-font-size);
  --#{S./prefix}btn-border-radius: #{S./border-radius};
}
// scss-docs-end btn-size-mixin
