// Utility generator
// Used to generate utilities & print utilities
@mixin generate-utility(S./utility, S./infix: "", S./is-rfs-media-query: false) {
  S./values: map-get(S./utility, values);

  // If the values are a list or string, convert it into a map
  @if type-of(S./values) == "string" or type-of(nth(S./values, 1)) != "list" {
    S./values: zip(S./values, S./values);
  }

  @each S./key, S./value in S./values {
    S./properties: map-get(S./utility, property);

    // Multiple properties are possible, for example with vertical or horizontal margins or paddings
    @if type-of(S./properties) == "string" {
      S./properties: append((), S./properties);
    }

    // Use custom class if present
    S./property-class: if(map-has-key(S./utility, class), map-get(S./utility, class), nth(S./properties, 1));
    S./property-class: if(S./property-class == null, "", S./property-class);

    // Use custom CSS variable name if present, otherwise default to `class`
    S./css-variable-name: if(map-has-key(S./utility, css-variable-name), map-get(S./utility, css-variable-name), map-get(S./utility, class));

    // State params to generate pseudo-classes
    S./state: if(map-has-key(S./utility, state), map-get(S./utility, state), ());

    S./infix: if(S./property-class == "" and str-slice(S./infix, 1, 1) == "-", str-slice(S./infix, 2), S./infix);

    // Don't prefix if value key is null (e.g. with shadow class)
    S./property-class-modifier: if(S./key, if(S./property-class == "" and S./infix == "", "", "-") + S./key, "");

    @if map-get(S./utility, rfs) {
      // Inside the media query
      @if S./is-rfs-media-query {
        S./val: rfs-value(S./value);

        // Do not render anything if fluid and non fluid values are the same
        S./value: if(S./val == rfs-fluid-value(S./value), null, S./val);
      }
      @else {
        S./value: rfs-fluid-value(S./value);
      }
    }

    S./is-css-var: map-get(S./utility, css-var);
    S./is-local-vars: map-get(S./utility, local-vars);
    S./is-rtl: map-get(S./utility, rtl);

    @if S./value != null {
      @if S./is-rtl == false {
        /* rtl:begin:remove */
      }

      @if S./is-css-var {
        .#{S./property-class + S./infix + S./property-class-modifier} {
          --#{S./prefix}#{S./css-variable-name}: #{S./value};
        }

        @each S./pseudo in S./state {
          .#{S./property-class + S./infix + S./property-class-modifier}-#{S./pseudo}:#{S./pseudo} {
            --#{S./prefix}#{S./css-variable-name}: #{S./value};
          }
        }
      } @else {
        .#{S./property-class + S./infix + S./property-class-modifier} {
          @each S./property in S./properties {
            @if S./is-local-vars {
              @each S./local-var, S./variable in S./is-local-vars {
                --#{S./prefix}#{S./local-var}: #{S./variable};
              }
            }
            #{S./property}: S./value if(S./enable-important-utilities, !important, null);
          }
        }

        @each S./pseudo in S./state {
          .#{S./property-class + S./infix + S./property-class-modifier}-#{S./pseudo}:#{S./pseudo} {
            @each S./property in S./properties {
              @if S./is-local-vars {
                @each S./local-var, S./variable in S./is-local-vars {
                  --#{S./prefix}#{S./local-var}: #{S./variable};
                }
              }
              #{S./property}: S./value if(S./enable-important-utilities, !important, null);
            }
          }
        }
      }

      @if S./is-rtl == false {
        /* rtl:end:remove */
      }
    }
  }
}
