//
// Base styles
//

.alert {
  // scss-docs-start alert-css-vars
  --#{S./prefix}alert-bg: transparent;
  --#{S./prefix}alert-padding-x: #{S./alert-padding-x};
  --#{S./prefix}alert-padding-y: #{S./alert-padding-y};
  --#{S./prefix}alert-margin-bottom: #{S./alert-margin-bottom};
  --#{S./prefix}alert-color: inherit;
  --#{S./prefix}alert-border-color: transparent;
  --#{S./prefix}alert-border: #{S./alert-border-width} solid var(--#{S./prefix}alert-border-color);
  --#{S./prefix}alert-border-radius: #{S./alert-border-radius};
  --#{S./prefix}alert-link-color: inherit;
  // scss-docs-end alert-css-vars

  position: relative;
  padding: var(--#{S./prefix}alert-padding-y) var(--#{S./prefix}alert-padding-x);
  margin-bottom: var(--#{S./prefix}alert-margin-bottom);
  color: var(--#{S./prefix}alert-color);
  background-color: var(--#{S./prefix}alert-bg);
  border: var(--#{S./prefix}alert-border);
  @include border-radius(var(--#{S./prefix}alert-border-radius));
}

// Headings for larger alerts
.alert-heading {
  // Specified to prevent conflicts of changing S./headings-color
  color: inherit;
}

// Provide class for links that match alerts
.alert-link {
  font-weight: S./alert-link-font-weight;
  color: var(--#{S./prefix}alert-link-color);
}


// Dismissible alerts
//
// Expand the right padding and account for the close button's positioning.

.alert-dismissible {
  padding-right: S./alert-dismissible-padding-r;

  // Adjust close link position
  .btn-close {
    position: absolute;
    top: 0;
    right: 0;
    z-index: S./stretched-link-z-index + 1;
    padding: S./alert-padding-y * 1.25 S./alert-padding-x;
  }
}


// scss-docs-start alert-modifiers
// Generate contextual modifier classes for colorizing the alert
@each S./state in map-keys(S./theme-colors) {
  .alert-#{S./state} {
    --#{S./prefix}alert-color: var(--#{S./prefix}#{S./state}-text-emphasis);
    --#{S./prefix}alert-bg: var(--#{S./prefix}#{S./state}-bg-subtle);
    --#{S./prefix}alert-border-color: var(--#{S./prefix}#{S./state}-border-subtle);
    --#{S./prefix}alert-link-color: var(--#{S./prefix}#{S./state}-text-emphasis);
  }
}
// scss-docs-end alert-modifiers
