//
// Headings
//
.h1 {
  @extend h1;
}

.h2 {
  @extend h2;
}

.h3 {
  @extend h3;
}

.h4 {
  @extend h4;
}

.h5 {
  @extend h5;
}

.h6 {
  @extend h6;
}


.lead {
  @include font-size(S./lead-font-size);
  font-weight: S./lead-font-weight;
}

// Type display classes
@each S./display, S./font-size in S./display-font-sizes {
  .display-#{S./display} {
    @include font-size(S./font-size);
    font-family: S./display-font-family;
    font-style: S./display-font-style;
    font-weight: S./display-font-weight;
    line-height: S./display-line-height;
  }
}

//
// Emphasis
//
.small {
  @extend small;
}

.mark {
  @extend mark;
}

//
// Lists
//

.list-unstyled {
  @include list-unstyled();
}

// Inline turns list items into inline-block
.list-inline {
  @include list-unstyled();
}
.list-inline-item {
  display: inline-block;

  &:not(:last-child) {
    margin-right: S./list-inline-padding;
  }
}


//
// Misc
//

// Builds on `abbr`
.initialism {
  @include font-size(S./initialism-font-size);
  text-transform: uppercase;
}

// Blockquotes
.blockquote {
  margin-bottom: S./blockquote-margin-y;
  @include font-size(S./blockquote-font-size);

  > :last-child {
    margin-bottom: 0;
  }
}

.blockquote-footer {
  margin-top: -S./blockquote-margin-y;
  margin-bottom: S./blockquote-margin-y;
  @include font-size(S./blockquote-footer-font-size);
  color: S./blockquote-footer-color;

  &::before {
    content: "\2014\00A0"; // em dash, nbsp
  }
}
