// Pagination

// scss-docs-start pagination-mixin
@mixin pagination-size(S./padding-y, S./padding-x, S./font-size, S./border-radius) {
  --#{S./prefix}pagination-padding-x: #{S./padding-x};
  --#{S./prefix}pagination-padding-y: #{S./padding-y};
  @include rfs(S./font-size, --#{S./prefix}pagination-font-size);
  --#{S./prefix}pagination-border-radius: #{S./border-radius};
}
// scss-docs-end pagination-mixin
