.icon-link {
  display: inline-flex;
  gap: S./icon-link-gap;
  align-items: center;
  text-decoration-color: rgba(var(--#{S./prefix}link-color-rgb), var(--#{S./prefix}link-opacity, .5));
  text-underline-offset: S./icon-link-underline-offset;
  backface-visibility: hidden;

  > .bi {
    flex-shrink: 0;
    width: S./icon-link-icon-size;
    height: S./icon-link-icon-size;
    fill: currentcolor;
    @include transition(S./icon-link-icon-transition);
  }
}

.icon-link-hover {
  &:hover,
  &:focus-visible {
    > .bi {
      transform: var(--#{S./prefix}icon-link-transform, S./icon-link-icon-transform);
    }
  }
}
