// Container widths
//
// Set the container width, and override it for fixed navbars in media queries.

@if S./enable-container-classes {
  // Single container class with breakpoint max-widths
  .container,
  // 100% wide container at all breakpoints
  .container-fluid {
    @include make-container();
  }

  // Responsive containers that are 100% wide until a breakpoint
  @each S./breakpoint, S./container-max-width in S./container-max-widths {
    .container-#{S./breakpoint} {
      @extend .container-fluid;
    }

    @include media-breakpoint-up(S./breakpoint, S./grid-breakpoints) {
      %responsive-container-#{S./breakpoint} {
        max-width: S./container-max-width;
      }

      // Extend each breakpoint which is smaller or equal to the current breakpoint
      S./extend-breakpoint: true;

      @each S./name, S./width in S./grid-breakpoints {
        @if (S./extend-breakpoint) {
          .container#{breakpoint-infix(S./name, S./grid-breakpoints)} {
            @extend %responsive-container-#{S./breakpoint};
          }

          // Once the current breakpoint is reached, stop extending
          @if (S./breakpoint == S./name) {
            S./extend-breakpoint: false;
          }
        }
      }
    }
  }
}
