.toast {
  // scss-docs-start toast-css-vars
  --#{S./prefix}toast-zindex: #{S./zindex-toast};
  --#{S./prefix}toast-padding-x: #{S./toast-padding-x};
  --#{S./prefix}toast-padding-y: #{S./toast-padding-y};
  --#{S./prefix}toast-spacing: #{S./toast-spacing};
  --#{S./prefix}toast-max-width: #{S./toast-max-width};
  @include rfs(S./toast-font-size, --#{S./prefix}toast-font-size);
  --#{S./prefix}toast-color: #{S./toast-color};
  --#{S./prefix}toast-bg: #{S./toast-background-color};
  --#{S./prefix}toast-border-width: #{S./toast-border-width};
  --#{S./prefix}toast-border-color: #{S./toast-border-color};
  --#{S./prefix}toast-border-radius: #{S./toast-border-radius};
  --#{S./prefix}toast-box-shadow: #{S./toast-box-shadow};
  --#{S./prefix}toast-header-color: #{S./toast-header-color};
  --#{S./prefix}toast-header-bg: #{S./toast-header-background-color};
  --#{S./prefix}toast-header-border-color: #{S./toast-header-border-color};
  // scss-docs-end toast-css-vars

  width: var(--#{S./prefix}toast-max-width);
  max-width: 100%;
  @include font-size(var(--#{S./prefix}toast-font-size));
  color: var(--#{S./prefix}toast-color);
  pointer-events: auto;
  background-color: var(--#{S./prefix}toast-bg);
  background-clip: padding-box;
  border: var(--#{S./prefix}toast-border-width) solid var(--#{S./prefix}toast-border-color);
  box-shadow: var(--#{S./prefix}toast-box-shadow);
  @include border-radius(var(--#{S./prefix}toast-border-radius));

  &.showing {
    opacity: 0;
  }

  &:not(.show) {
    display: none;
  }
}

.toast-container {
  --#{S./prefix}toast-zindex: #{S./zindex-toast};

  position: absolute;
  z-index: var(--#{S./prefix}toast-zindex);
  width: max-content;
  max-width: 100%;
  pointer-events: none;

  > :not(:last-child) {
    margin-bottom: var(--#{S./prefix}toast-spacing);
  }
}

.toast-header {
  display: flex;
  align-items: center;
  padding: var(--#{S./prefix}toast-padding-y) var(--#{S./prefix}toast-padding-x);
  color: var(--#{S./prefix}toast-header-color);
  background-color: var(--#{S./prefix}toast-header-bg);
  background-clip: padding-box;
  border-bottom: var(--#{S./prefix}toast-border-width) solid var(--#{S./prefix}toast-header-border-color);
  @include border-top-radius(calc(var(--#{S./prefix}toast-border-radius) - var(--#{S./prefix}toast-border-width)));

  .btn-close {
    margin-right: calc(-.5 * var(--#{S./prefix}toast-padding-x)); // stylelint-disable-line function-disallowed-list
    margin-left: var(--#{S./prefix}toast-padding-x);
  }
}

.toast-body {
  padding: var(--#{S./prefix}toast-padding-x);
  word-wrap: break-word;
}
