// Shared between modals and offcanvases
@mixin overlay-backdrop(S./zindex, S./backdrop-bg, S./backdrop-opacity) {
  position: fixed;
  top: 0;
  left: 0;
  z-index: S./zindex;
  width: 100vw;
  height: 100vh;
  background-color: S./backdrop-bg;

  // Fade for backdrop
  &.fade { opacity: 0; }
  &.show { opacity: S./backdrop-opacity; }
}
