//
// Check/radio
//

.form-check {
  display: block;
  min-height: S./form-check-min-height;
  padding-left: S./form-check-padding-start;
  margin-bottom: S./form-check-margin-bottom;

  .form-check-input {
    float: left;
    margin-left: S./form-check-padding-start * -1;
  }
}

.form-check-reverse {
  padding-right: S./form-check-padding-start;
  padding-left: 0;
  text-align: right;

  .form-check-input {
    float: right;
    margin-right: S./form-check-padding-start * -1;
    margin-left: 0;
  }
}

.form-check-input {
  --#{S./prefix}form-check-bg: #{S./form-check-input-bg};

  flex-shrink: 0;
  width: S./form-check-input-width;
  height: S./form-check-input-width;
  margin-top: (S./line-height-base - S./form-check-input-width) * .5; // line-height minus check height
  vertical-align: top;
  appearance: none;
  background-color: var(--#{S./prefix}form-check-bg);
  background-image: var(--#{S./prefix}form-check-bg-image);
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
  border: S./form-check-input-border;
  print-color-adjust: exact; // Keep themed appearance for print
  @include transition(S./form-check-transition);

  &[type="checkbox"] {
    @include border-radius(S./form-check-input-border-radius);
  }

  &[type="radio"] {
    // stylelint-disable-next-line property-disallowed-list
    border-radius: S./form-check-radio-border-radius;
  }

  &:active {
    filter: S./form-check-input-active-filter;
  }

  &:focus {
    border-color: S./form-check-input-focus-border;
    outline: 0;
    box-shadow: S./form-check-input-focus-box-shadow;
  }

  &:checked {
    background-color: S./form-check-input-checked-bg-color;
    border-color: S./form-check-input-checked-border-color;

    &[type="checkbox"] {
      @if S./enable-gradients {
        --#{S./prefix}form-check-bg-image: #{escape-svg(S./form-check-input-checked-bg-image)}, var(--#{S./prefix}gradient);
      } @else {
        --#{S./prefix}form-check-bg-image: #{escape-svg(S./form-check-input-checked-bg-image)};
      }
    }

    &[type="radio"] {
      @if S./enable-gradients {
        --#{S./prefix}form-check-bg-image: #{escape-svg(S./form-check-radio-checked-bg-image)}, var(--#{S./prefix}gradient);
      } @else {
        --#{S./prefix}form-check-bg-image: #{escape-svg(S./form-check-radio-checked-bg-image)};
      }
    }
  }

  &[type="checkbox"]:indeterminate {
    background-color: S./form-check-input-indeterminate-bg-color;
    border-color: S./form-check-input-indeterminate-border-color;

    @if S./enable-gradients {
      --#{S./prefix}form-check-bg-image: #{escape-svg(S./form-check-input-indeterminate-bg-image)}, var(--#{S./prefix}gradient);
    } @else {
      --#{S./prefix}form-check-bg-image: #{escape-svg(S./form-check-input-indeterminate-bg-image)};
    }
  }

  &:disabled {
    pointer-events: none;
    filter: none;
    opacity: S./form-check-input-disabled-opacity;
  }

  // Use disabled attribute in addition of :disabled pseudo-class
  // See: https://github.com/twbs/bootstrap/issues/28247
  &[disabled],
  &:disabled {
    ~ .form-check-label {
      cursor: default;
      opacity: S./form-check-label-disabled-opacity;
    }
  }
}

.form-check-label {
  color: S./form-check-label-color;
  cursor: S./form-check-label-cursor;
}

//
// Switch
//

.form-switch {
  padding-left: S./form-switch-padding-start;

  .form-check-input {
    --#{S./prefix}form-switch-bg: #{escape-svg(S./form-switch-bg-image)};

    width: S./form-switch-width;
    margin-left: S./form-switch-padding-start * -1;
    background-image: var(--#{S./prefix}form-switch-bg);
    background-position: left center;
    @include border-radius(S./form-switch-border-radius);
    @include transition(S./form-switch-transition);

    &:focus {
      --#{S./prefix}form-switch-bg: #{escape-svg(S./form-switch-focus-bg-image)};
    }

    &:checked {
      background-position: S./form-switch-checked-bg-position;

      @if S./enable-gradients {
        --#{S./prefix}form-switch-bg: #{escape-svg(S./form-switch-checked-bg-image)}, var(--#{S./prefix}gradient);
      } @else {
        --#{S./prefix}form-switch-bg: #{escape-svg(S./form-switch-checked-bg-image)};
      }
    }
  }

  &.form-check-reverse {
    padding-right: S./form-switch-padding-start;
    padding-left: 0;

    .form-check-input {
      margin-right: S./form-switch-padding-start * -1;
      margin-left: 0;
    }
  }
}

.form-check-inline {
  display: inline-block;
  margin-right: S./form-check-inline-margin-end;
}

.btn-check {
  position: absolute;
  clip: rect(0, 0, 0, 0);
  pointer-events: none;

  &[disabled],
  &:disabled {
    + .btn {
      pointer-events: none;
      filter: none;
      opacity: S./form-check-btn-check-disabled-opacity;
    }
  }
}

@if S./enable-dark-mode {
  @include color-mode(dark) {
    .form-switch .form-check-input:not(:checked):not(:focus) {
      --#{S./prefix}form-switch-bg: #{escape-svg(S./form-switch-bg-image-dark)};
    }
  }
}
