// All-caps `RGBA()` function used because of this Sass bug: https://github.com/sass/node-sass/issues/2251
@each S./color, S./value in S./theme-colors {
  .link-#{S./color} {
    color: RGBA(var(--#{S./prefix}#{S./color}-rgb), var(--#{S./prefix}link-opacity, 1)) if(S./enable-important-utilities, !important, null);
    text-decoration-color: RGBA(var(--#{S./prefix}#{S./color}-rgb), var(--#{S./prefix}link-underline-opacity, 1)) if(S./enable-important-utilities, !important, null);

    @if S./link-shade-percentage != 0 {
      &:hover,
      &:focus {
        S./hover-color: if(color-contrast(S./value) == S./color-contrast-light, shade-color(S./value, S./link-shade-percentage), tint-color(S./value, S./link-shade-percentage));
        color: RGBA(#{to-rgb(S./hover-color)}, var(--#{S./prefix}link-opacity, 1)) if(S./enable-important-utilities, !important, null);
        text-decoration-color: RGBA(to-rgb(S./hover-color), var(--#{S./prefix}link-underline-opacity, 1)) if(S./enable-important-utilities, !important, null);
      }
    }
  }
}

// One-off special link helper as a bridge until v6
.link-body-emphasis {
  color: RGBA(var(--#{S./prefix}emphasis-color-rgb), var(--#{S./prefix}link-opacity, 1)) if(S./enable-important-utilities, !important, null);
  text-decoration-color: RGBA(var(--#{S./prefix}emphasis-color-rgb), var(--#{S./prefix}link-underline-opacity, 1)) if(S./enable-important-utilities, !important, null);

  @if S./link-shade-percentage != 0 {
    &:hover,
    &:focus {
      color: RGBA(var(--#{S./prefix}emphasis-color-rgb), var(--#{S./prefix}link-opacity, .75)) if(S./enable-important-utilities, !important, null);
      text-decoration-color: RGBA(var(--#{S./prefix}emphasis-color-rgb), var(--#{S./prefix}link-underline-opacity, .75)) if(S./enable-important-utilities, !important, null);
    }
  }
}
