// scss-docs-start table-variant
@mixin table-variant(S./state, S./background) {
  .table-#{S./state} {
    S./color: color-contrast(opaque(S./body-bg, S./background));
    S./hover-bg: mix(S./color, S./background, percentage(S./table-hover-bg-factor));
    S./striped-bg: mix(S./color, S./background, percentage(S./table-striped-bg-factor));
    S./active-bg: mix(S./color, S./background, percentage(S./table-active-bg-factor));
    S./table-border-color: mix(S./color, S./background, percentage(S./table-border-factor));

    --#{S./prefix}table-color: #{S./color};
    --#{S./prefix}table-bg: #{S./background};
    --#{S./prefix}table-border-color: #{S./table-border-color};
    --#{S./prefix}table-striped-bg: #{S./striped-bg};
    --#{S./prefix}table-striped-color: #{color-contrast(S./striped-bg)};
    --#{S./prefix}table-active-bg: #{S./active-bg};
    --#{S./prefix}table-active-color: #{color-contrast(S./active-bg)};
    --#{S./prefix}table-hover-bg: #{S./hover-bg};
    --#{S./prefix}table-hover-color: #{color-contrast(S./hover-bg)};

    color: var(--#{S./prefix}table-color);
    border-color: var(--#{S./prefix}table-border-color);
  }
}
// scss-docs-end table-variant
