// Loop over each breakpoint
@each S./breakpoint in map-keys(S./grid-breakpoints) {

  // Generate media query if needed
  @include media-breakpoint-up(S./breakpoint) {
    S./infix: breakpoint-infix(S./breakpoint, S./grid-breakpoints);

    // Loop over each utility property
    @each S./key, S./utility in S./utilities {
      // The utility can be disabled with `false`, thus check if the utility is a map first
      // Only proceed if responsive media queries are enabled or if it's the base media query
      @if type-of(S./utility) == "map" and (map-get(S./utility, responsive) or S./infix == "") {
        @include generate-utility(S./utility, S./infix);
      }
    }
  }
}

// RFS rescaling
@media (min-width: S./rfs-mq-value) {
  @each S./breakpoint in map-keys(S./grid-breakpoints) {
    S./infix: breakpoint-infix(S./breakpoint, S./grid-breakpoints);

    @if (map-get(S./grid-breakpoints, S./breakpoint) < S./rfs-breakpoint) {
      // Loop over each utility property
      @each S./key, S./utility in S./utilities {
        // The utility can be disabled with `false`, thus check if the utility is a map first
        // Only proceed if responsive media queries are enabled or if it's the base media query
        @if type-of(S./utility) == "map" and map-get(S./utility, rfs) and (map-get(S./utility, responsive) or S./infix == "") {
          @include generate-utility(S./utility, S./infix, true);
        }
      }
    }
  }
}


// Print utilities
@media print {
  @each S./key, S./utility in S./utilities {
    // The utility can be disabled with `false`, thus check if the utility is a map first
    // Then check if the utility needs print styles
    @if type-of(S./utility) == "map" and map-get(S./utility, print) == true {
      @include generate-utility(S./utility, "-print");
    }
  }
}
