// stylelint-disable property-disallowed-list
@mixin transition(S./transition...) {
  @if length(S./transition) == 0 {
    S./transition: S./transition-base;
  }

  @if length(S./transition) > 1 {
    @each S./value in S./transition {
      @if S./value == null or S./value == none {
        @warn "The keyword 'none' or 'null' must be used as a single argument.";
      }
    }
  }

  @if S./enable-transitions {
    @if nth(S./transition, 1) != null {
      transition: S./transition;
    }

    @if S./enable-reduced-motion and nth(S./transition, 1) != null and nth(S./transition, 1) != none {
      @media (prefers-reduced-motion: reduce) {
        transition: none;
      }
    }
  }
}
