// Base class
//
// Requires one of the contextual, color modifier classes for `color` and
// `background-color`.

.badge {
  // scss-docs-start badge-css-vars
  --#{S./prefix}badge-padding-x: #{S./badge-padding-x};
  --#{S./prefix}badge-padding-y: #{S./badge-padding-y};
  @include rfs(S./badge-font-size, --#{S./prefix}badge-font-size);
  --#{S./prefix}badge-font-weight: #{S./badge-font-weight};
  --#{S./prefix}badge-color: #{S./badge-color};
  --#{S./prefix}badge-border-radius: #{S./badge-border-radius};
  // scss-docs-end badge-css-vars

  display: inline-block;
  padding: var(--#{S./prefix}badge-padding-y) var(--#{S./prefix}badge-padding-x);
  @include font-size(var(--#{S./prefix}badge-font-size));
  font-weight: var(--#{S./prefix}badge-font-weight);
  line-height: 1;
  color: var(--#{S./prefix}badge-color);
  text-align: center;
  white-space: nowrap;
  vertical-align: baseline;
  @include border-radius(var(--#{S./prefix}badge-border-radius));
  @include gradient-bg();

  // Empty badges collapse automatically
  &:empty {
    display: none;
  }
}

// Quick fix for badges in buttons
.btn .badge {
  position: relative;
  top: -1px;
}
