// Credit: Nicolas Gallagher and SUIT CSS.

.ratio {
  position: relative;
  width: 100%;

  &::before {
    display: block;
    padding-top: var(--#{S./prefix}aspect-ratio);
    content: "";
  }

  > * {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }
}

@each S./key, S./ratio in S./aspect-ratios {
  .ratio-#{S./key} {
    --#{S./prefix}aspect-ratio: #{S./ratio};
  }
}
