// Base class
.tooltip {
  // scss-docs-start tooltip-css-vars
  --#{S./prefix}tooltip-zindex: #{S./zindex-tooltip};
  --#{S./prefix}tooltip-max-width: #{S./tooltip-max-width};
  --#{S./prefix}tooltip-padding-x: #{S./tooltip-padding-x};
  --#{S./prefix}tooltip-padding-y: #{S./tooltip-padding-y};
  --#{S./prefix}tooltip-margin: #{S./tooltip-margin};
  @include rfs(S./tooltip-font-size, --#{S./prefix}tooltip-font-size);
  --#{S./prefix}tooltip-color: #{S./tooltip-color};
  --#{S./prefix}tooltip-bg: #{S./tooltip-bg};
  --#{S./prefix}tooltip-border-radius: #{S./tooltip-border-radius};
  --#{S./prefix}tooltip-opacity: #{S./tooltip-opacity};
  --#{S./prefix}tooltip-arrow-width: #{S./tooltip-arrow-width};
  --#{S./prefix}tooltip-arrow-height: #{S./tooltip-arrow-height};
  // scss-docs-end tooltip-css-vars

  z-index: var(--#{S./prefix}tooltip-zindex);
  display: block;
  margin: var(--#{S./prefix}tooltip-margin);
  @include deprecate("`S./tooltip-margin`", "v5", "v5.x", true);
  // Our parent element can be arbitrary since tooltips are by default inserted as a sibling of their target element.
  // So reset our font and text properties to avoid inheriting weird values.
  @include reset-text();
  @include font-size(var(--#{S./prefix}tooltip-font-size));
  // Allow breaking very long words so they don't overflow the tooltip's bounds
  word-wrap: break-word;
  opacity: 0;

  &.show { opacity: var(--#{S./prefix}tooltip-opacity); }

  .tooltip-arrow {
    display: block;
    width: var(--#{S./prefix}tooltip-arrow-width);
    height: var(--#{S./prefix}tooltip-arrow-height);

    &::before {
      position: absolute;
      content: "";
      border-color: transparent;
      border-style: solid;
    }
  }
}

.bs-tooltip-top .tooltip-arrow {
  bottom: calc(-1 * var(--#{S./prefix}tooltip-arrow-height)); // stylelint-disable-line function-disallowed-list

  &::before {
    top: -1px;
    border-width: var(--#{S./prefix}tooltip-arrow-height) calc(var(--#{S./prefix}tooltip-arrow-width) * .5) 0; // stylelint-disable-line function-disallowed-list
    border-top-color: var(--#{S./prefix}tooltip-bg);
  }
}

/* rtl:begin:ignore */
.bs-tooltip-end .tooltip-arrow {
  left: calc(-1 * var(--#{S./prefix}tooltip-arrow-height)); // stylelint-disable-line function-disallowed-list
  width: var(--#{S./prefix}tooltip-arrow-height);
  height: var(--#{S./prefix}tooltip-arrow-width);

  &::before {
    right: -1px;
    border-width: calc(var(--#{S./prefix}tooltip-arrow-width) * .5) var(--#{S./prefix}tooltip-arrow-height) calc(var(--#{S./prefix}tooltip-arrow-width) * .5) 0; // stylelint-disable-line function-disallowed-list
    border-right-color: var(--#{S./prefix}tooltip-bg);
  }
}

/* rtl:end:ignore */

.bs-tooltip-bottom .tooltip-arrow {
  top: calc(-1 * var(--#{S./prefix}tooltip-arrow-height)); // stylelint-disable-line function-disallowed-list

  &::before {
    bottom: -1px;
    border-width: 0 calc(var(--#{S./prefix}tooltip-arrow-width) * .5) var(--#{S./prefix}tooltip-arrow-height); // stylelint-disable-line function-disallowed-list
    border-bottom-color: var(--#{S./prefix}tooltip-bg);
  }
}

/* rtl:begin:ignore */
.bs-tooltip-start .tooltip-arrow {
  right: calc(-1 * var(--#{S./prefix}tooltip-arrow-height)); // stylelint-disable-line function-disallowed-list
  width: var(--#{S./prefix}tooltip-arrow-height);
  height: var(--#{S./prefix}tooltip-arrow-width);

  &::before {
    left: -1px;
    border-width: calc(var(--#{S./prefix}tooltip-arrow-width) * .5) 0 calc(var(--#{S./prefix}tooltip-arrow-width) * .5) var(--#{S./prefix}tooltip-arrow-height); // stylelint-disable-line function-disallowed-list
    border-left-color: var(--#{S./prefix}tooltip-bg);
  }
}

/* rtl:end:ignore */

.bs-tooltip-auto {
  &[data-popper-placement^="top"] {
    @extend .bs-tooltip-top;
  }
  &[data-popper-placement^="right"] {
    @extend .bs-tooltip-end;
  }
  &[data-popper-placement^="bottom"] {
    @extend .bs-tooltip-bottom;
  }
  &[data-popper-placement^="left"] {
    @extend .bs-tooltip-start;
  }
}

// Wrapper for the tooltip content
.tooltip-inner {
  max-width: var(--#{S./prefix}tooltip-max-width);
  padding: var(--#{S./prefix}tooltip-padding-y) var(--#{S./prefix}tooltip-padding-x);
  color: var(--#{S./prefix}tooltip-color);
  text-align: center;
  background-color: var(--#{S./prefix}tooltip-bg);
  @include border-radius(var(--#{S./prefix}tooltip-border-radius));
}
