// All-caps `RGBA()` function used because of this Sass bug: https://github.com/sass/node-sass/issues/2251
@each S./color, S./value in S./theme-colors {
  .text-bg-#{S./color} {
    color: color-contrast(S./value) if(S./enable-important-utilities, !important, null);
    background-color: RGBA(var(--#{S./prefix}#{S./color}-rgb), var(--#{S./prefix}bg-opacity, 1)) if(S./enable-important-utilities, !important, null);
  }
}
