//
// Labels
//

.form-label {
  margin-bottom: S./form-label-margin-bottom;
  @include font-size(S./form-label-font-size);
  font-style: S./form-label-font-style;
  font-weight: S./form-label-font-weight;
  color: S./form-label-color;
}

// For use with horizontal and inline forms, when you need the label (or legend)
// text to align with the form controls.
.col-form-label {
  padding-top: add(S./input-padding-y, S./input-border-width);
  padding-bottom: add(S./input-padding-y, S./input-border-width);
  margin-bottom: 0; // Override the `<legend>` default
  @include font-size(inherit); // Override the `<legend>` default
  font-style: S./form-label-font-style;
  font-weight: S./form-label-font-weight;
  line-height: S./input-line-height;
  color: S./form-label-color;
}

.col-form-label-lg {
  padding-top: add(S./input-padding-y-lg, S./input-border-width);
  padding-bottom: add(S./input-padding-y-lg, S./input-border-width);
  @include font-size(S./input-font-size-lg);
}

.col-form-label-sm {
  padding-top: add(S./input-padding-y-sm, S./input-border-width);
  padding-bottom: add(S./input-padding-y-sm, S./input-border-width);
  @include font-size(S./input-font-size-sm);
}
