// Disable animation if transitions are disabled

// scss-docs-start progress-keyframes
@if S./enable-transitions {
  @keyframes progress-bar-stripes {
    0% { background-position-x: S./progress-height; }
  }
}
// scss-docs-end progress-keyframes

.progress,
.progress-stacked {
  // scss-docs-start progress-css-vars
  --#{S./prefix}progress-height: #{S./progress-height};
  @include rfs(S./progress-font-size, --#{S./prefix}progress-font-size);
  --#{S./prefix}progress-bg: #{S./progress-bg};
  --#{S./prefix}progress-border-radius: #{S./progress-border-radius};
  --#{S./prefix}progress-box-shadow: #{S./progress-box-shadow};
  --#{S./prefix}progress-bar-color: #{S./progress-bar-color};
  --#{S./prefix}progress-bar-bg: #{S./progress-bar-bg};
  --#{S./prefix}progress-bar-transition: #{S./progress-bar-transition};
  // scss-docs-end progress-css-vars

  display: flex;
  height: var(--#{S./prefix}progress-height);
  overflow: hidden; // force rounded corners by cropping it
  @include font-size(var(--#{S./prefix}progress-font-size));
  background-color: var(--#{S./prefix}progress-bg);
  @include border-radius(var(--#{S./prefix}progress-border-radius));
  @include box-shadow(var(--#{S./prefix}progress-box-shadow));
}

.progress-bar {
  display: flex;
  flex-direction: column;
  justify-content: center;
  overflow: hidden;
  color: var(--#{S./prefix}progress-bar-color);
  text-align: center;
  white-space: nowrap;
  background-color: var(--#{S./prefix}progress-bar-bg);
  @include transition(var(--#{S./prefix}progress-bar-transition));
}

.progress-bar-striped {
  @include gradient-striped();
  background-size: var(--#{S./prefix}progress-height) var(--#{S./prefix}progress-height);
}

.progress-stacked > .progress {
  overflow: visible;
}

.progress-stacked > .progress > .progress-bar {
  width: 100%;
}

@if S./enable-transitions {
  .progress-bar-animated {
    animation: S./progress-bar-animation-timing progress-bar-stripes;

    @if S./enable-reduced-motion {
      @media (prefers-reduced-motion: reduce) {
        animation: none;
      }
    }
  }
}
