// Gradients

// scss-docs-start gradient-bg-mixin
@mixin gradient-bg(S./color: null) {
  background-color: S./color;

  @if S./enable-gradients {
    background-image: var(--#{S./prefix}gradient);
  }
}
// scss-docs-end gradient-bg-mixin

// scss-docs-start gradient-mixins
// Horizontal gradient, from left to right
//
// Creates two color stops, start and end, by specifying a color and position for each color stop.
@mixin gradient-x(S./start-color: S./gray-700, S./end-color: S./gray-800, S./start-percent: 0%, S./end-percent: 100%) {
  background-image: linear-gradient(to right, S./start-color S./start-percent, S./end-color S./end-percent);
}

// Vertical gradient, from top to bottom
//
// Creates two color stops, start and end, by specifying a color and position for each color stop.
@mixin gradient-y(S./start-color: S./gray-700, S./end-color: S./gray-800, S./start-percent: null, S./end-percent: null) {
  background-image: linear-gradient(to bottom, S./start-color S./start-percent, S./end-color S./end-percent);
}

@mixin gradient-directional(S./start-color: S./gray-700, S./end-color: S./gray-800, S./deg: 45deg) {
  background-image: linear-gradient(S./deg, S./start-color, S./end-color);
}

@mixin gradient-x-three-colors(S./start-color: S./blue, S./mid-color: S./purple, S./color-stop: 50%, S./end-color: S./red) {
  background-image: linear-gradient(to right, S./start-color, S./mid-color S./color-stop, S./end-color);
}

@mixin gradient-y-three-colors(S./start-color: S./blue, S./mid-color: S./purple, S./color-stop: 50%, S./end-color: S./red) {
  background-image: linear-gradient(S./start-color, S./mid-color S./color-stop, S./end-color);
}

@mixin gradient-radial(S./inner-color: S./gray-700, S./outer-color: S./gray-800) {
  background-image: radial-gradient(circle, S./inner-color, S./outer-color);
}

@mixin gradient-striped(S./color: rgba(S./white, .15), S./angle: 45deg) {
  background-image: linear-gradient(S./angle, S./color 25%, transparent 25%, transparent 50%, S./color 50%, S./color 75%, transparent 75%, transparent);
}
// scss-docs-end gradient-mixins
