.pagination {
  // scss-docs-start pagination-css-vars
  --#{S./prefix}pagination-padding-x: #{S./pagination-padding-x};
  --#{S./prefix}pagination-padding-y: #{S./pagination-padding-y};
  @include rfs(S./pagination-font-size, --#{S./prefix}pagination-font-size);
  --#{S./prefix}pagination-color: #{S./pagination-color};
  --#{S./prefix}pagination-bg: #{S./pagination-bg};
  --#{S./prefix}pagination-border-width: #{S./pagination-border-width};
  --#{S./prefix}pagination-border-color: #{S./pagination-border-color};
  --#{S./prefix}pagination-border-radius: #{S./pagination-border-radius};
  --#{S./prefix}pagination-hover-color: #{S./pagination-hover-color};
  --#{S./prefix}pagination-hover-bg: #{S./pagination-hover-bg};
  --#{S./prefix}pagination-hover-border-color: #{S./pagination-hover-border-color};
  --#{S./prefix}pagination-focus-color: #{S./pagination-focus-color};
  --#{S./prefix}pagination-focus-bg: #{S./pagination-focus-bg};
  --#{S./prefix}pagination-focus-box-shadow: #{S./pagination-focus-box-shadow};
  --#{S./prefix}pagination-active-color: #{S./pagination-active-color};
  --#{S./prefix}pagination-active-bg: #{S./pagination-active-bg};
  --#{S./prefix}pagination-active-border-color: #{S./pagination-active-border-color};
  --#{S./prefix}pagination-disabled-color: #{S./pagination-disabled-color};
  --#{S./prefix}pagination-disabled-bg: #{S./pagination-disabled-bg};
  --#{S./prefix}pagination-disabled-border-color: #{S./pagination-disabled-border-color};
  // scss-docs-end pagination-css-vars

  display: flex;
  @include list-unstyled();
}

.page-link {
  position: relative;
  display: block;
  padding: var(--#{S./prefix}pagination-padding-y) var(--#{S./prefix}pagination-padding-x);
  @include font-size(var(--#{S./prefix}pagination-font-size));
  color: var(--#{S./prefix}pagination-color);
  text-decoration: if(S./link-decoration == none, null, none);
  background-color: var(--#{S./prefix}pagination-bg);
  border: var(--#{S./prefix}pagination-border-width) solid var(--#{S./prefix}pagination-border-color);
  @include transition(S./pagination-transition);

  &:hover {
    z-index: 2;
    color: var(--#{S./prefix}pagination-hover-color);
    text-decoration: if(S./link-hover-decoration == underline, none, null);
    background-color: var(--#{S./prefix}pagination-hover-bg);
    border-color: var(--#{S./prefix}pagination-hover-border-color);
  }

  &:focus {
    z-index: 3;
    color: var(--#{S./prefix}pagination-focus-color);
    background-color: var(--#{S./prefix}pagination-focus-bg);
    outline: S./pagination-focus-outline;
    box-shadow: var(--#{S./prefix}pagination-focus-box-shadow);
  }

  &.active,
  .active > & {
    z-index: 3;
    color: var(--#{S./prefix}pagination-active-color);
    @include gradient-bg(var(--#{S./prefix}pagination-active-bg));
    border-color: var(--#{S./prefix}pagination-active-border-color);
  }

  &.disabled,
  .disabled > & {
    color: var(--#{S./prefix}pagination-disabled-color);
    pointer-events: none;
    background-color: var(--#{S./prefix}pagination-disabled-bg);
    border-color: var(--#{S./prefix}pagination-disabled-border-color);
  }
}

.page-item {
  &:not(:first-child) .page-link {
    margin-left: S./pagination-margin-start;
  }

  @if S./pagination-margin-start == calc(#{S./pagination-border-width} * -1) {
    &:first-child {
      .page-link {
        @include border-start-radius(var(--#{S./prefix}pagination-border-radius));
      }
    }

    &:last-child {
      .page-link {
        @include border-end-radius(var(--#{S./prefix}pagination-border-radius));
      }
    }
  } @else {
    // Add border-radius to all pageLinks in case they have left margin
    .page-link {
      @include border-radius(var(--#{S./prefix}pagination-border-radius));
    }
  }
}


//
// Sizing
//

.pagination-lg {
  @include pagination-size(S./pagination-padding-y-lg, S./pagination-padding-x-lg, S./font-size-lg, S./pagination-border-radius-lg);
}

.pagination-sm {
  @include pagination-size(S./pagination-padding-y-sm, S./pagination-padding-x-sm, S./font-size-sm, S./pagination-border-radius-sm);
}
