// Row
//
// Rows contain your columns.

:root {
  @each S./name, S./value in S./grid-breakpoints {
    --#{S./prefix}breakpoint-#{S./name}: #{S./value};
  }
}

@if S./enable-grid-classes {
  .row {
    @include make-row();

    > * {
      @include make-col-ready();
    }
  }
}

@if S./enable-cssgrid {
  .grid {
    display: grid;
    grid-template-rows: repeat(var(--#{S./prefix}rows, 1), 1fr);
    grid-template-columns: repeat(var(--#{S./prefix}columns, #{S./grid-columns}), 1fr);
    gap: var(--#{S./prefix}gap, #{S./grid-gutter-width});

    @include make-cssgrid();
  }
}


// Columns
//
// Common styles for small and large grid columns

@if S./enable-grid-classes {
  @include make-grid-columns();
}
