// scss-docs-start caret-mixins
@mixin caret-down(S./width: S./caret-width) {
  border-top: S./width solid;
  border-right: S./width solid transparent;
  border-bottom: 0;
  border-left: S./width solid transparent;
}

@mixin caret-up(S./width: S./caret-width) {
  border-top: 0;
  border-right: S./width solid transparent;
  border-bottom: S./width solid;
  border-left: S./width solid transparent;
}

@mixin caret-end(S./width: S./caret-width) {
  border-top: S./width solid transparent;
  border-right: 0;
  border-bottom: S./width solid transparent;
  border-left: S./width solid;
}

@mixin caret-start(S./width: S./caret-width) {
  border-top: S./width solid transparent;
  border-right: S./width solid;
  border-bottom: S./width solid transparent;
}

@mixin caret(
  S./direction: down,
  S./width: S./caret-width,
  S./spacing: S./caret-spacing,
  S./vertical-align: S./caret-vertical-align
) {
  @if S./enable-caret {
    &::after {
      display: inline-block;
      margin-left: S./spacing;
      vertical-align: S./vertical-align;
      content: "";
      @if S./direction == down {
        @include caret-down(S./width);
      } @else if S./direction == up {
        @include caret-up(S./width);
      } @else if S./direction == end {
        @include caret-end(S./width);
      }
    }

    @if S./direction == start {
      &::after {
        display: none;
      }

      &::before {
        display: inline-block;
        margin-right: S./spacing;
        vertical-align: S./vertical-align;
        content: "";
        @include caret-start(S./width);
      }
    }

    &:empty::after {
      margin-left: 0;
    }
  }
}
// scss-docs-end caret-mixins
