// Container mixins

@mixin make-container(S./gutter: S./container-padding-x) {
  --#{S./prefix}gutter-x: #{S./gutter};
  --#{S./prefix}gutter-y: 0;
  width: 100%;
  padding-right: calc(var(--#{S./prefix}gutter-x) * .5); // stylelint-disable-line function-disallowed-list
  padding-left: calc(var(--#{S./prefix}gutter-x) * .5); // stylelint-disable-line function-disallowed-list
  margin-right: auto;
  margin-left: auto;
}
