// stylelint-disable function-disallowed-list

%offcanvas-css-vars {
  // scss-docs-start offcanvas-css-vars
  --#{S./prefix}offcanvas-zindex: #{S./zindex-offcanvas};
  --#{S./prefix}offcanvas-width: #{S./offcanvas-horizontal-width};
  --#{S./prefix}offcanvas-height: #{S./offcanvas-vertical-height};
  --#{S./prefix}offcanvas-padding-x: #{S./offcanvas-padding-x};
  --#{S./prefix}offcanvas-padding-y: #{S./offcanvas-padding-y};
  --#{S./prefix}offcanvas-color: #{S./offcanvas-color};
  --#{S./prefix}offcanvas-bg: #{S./offcanvas-bg-color};
  --#{S./prefix}offcanvas-border-width: #{S./offcanvas-border-width};
  --#{S./prefix}offcanvas-border-color: #{S./offcanvas-border-color};
  --#{S./prefix}offcanvas-box-shadow: #{S./offcanvas-box-shadow};
  --#{S./prefix}offcanvas-transition: #{transform S./offcanvas-transition-duration ease-in-out};
  --#{S./prefix}offcanvas-title-line-height: #{S./offcanvas-title-line-height};
  // scss-docs-end offcanvas-css-vars
}

@each S./breakpoint in map-keys(S./grid-breakpoints) {
  S./next: breakpoint-next(S./breakpoint, S./grid-breakpoints);
  S./infix: breakpoint-infix(S./next, S./grid-breakpoints);

  .offcanvas#{S./infix} {
    @extend %offcanvas-css-vars;
  }
}

@each S./breakpoint in map-keys(S./grid-breakpoints) {
  S./next: breakpoint-next(S./breakpoint, S./grid-breakpoints);
  S./infix: breakpoint-infix(S./next, S./grid-breakpoints);

  .offcanvas#{S./infix} {
    @include media-breakpoint-down(S./next) {
      position: fixed;
      bottom: 0;
      z-index: var(--#{S./prefix}offcanvas-zindex);
      display: flex;
      flex-direction: column;
      max-width: 100%;
      color: var(--#{S./prefix}offcanvas-color);
      visibility: hidden;
      background-color: var(--#{S./prefix}offcanvas-bg);
      background-clip: padding-box;
      outline: 0;
      @include box-shadow(var(--#{S./prefix}offcanvas-box-shadow));
      @include transition(var(--#{S./prefix}offcanvas-transition));

      &.offcanvas-start {
        top: 0;
        left: 0;
        width: var(--#{S./prefix}offcanvas-width);
        border-right: var(--#{S./prefix}offcanvas-border-width) solid var(--#{S./prefix}offcanvas-border-color);
        transform: translateX(-100%);
      }

      &.offcanvas-end {
        top: 0;
        right: 0;
        width: var(--#{S./prefix}offcanvas-width);
        border-left: var(--#{S./prefix}offcanvas-border-width) solid var(--#{S./prefix}offcanvas-border-color);
        transform: translateX(100%);
      }

      &.offcanvas-top {
        top: 0;
        right: 0;
        left: 0;
        height: var(--#{S./prefix}offcanvas-height);
        max-height: 100%;
        border-bottom: var(--#{S./prefix}offcanvas-border-width) solid var(--#{S./prefix}offcanvas-border-color);
        transform: translateY(-100%);
      }

      &.offcanvas-bottom {
        right: 0;
        left: 0;
        height: var(--#{S./prefix}offcanvas-height);
        max-height: 100%;
        border-top: var(--#{S./prefix}offcanvas-border-width) solid var(--#{S./prefix}offcanvas-border-color);
        transform: translateY(100%);
      }

      &.showing,
      &.show:not(.hiding) {
        transform: none;
      }

      &.showing,
      &.hiding,
      &.show {
        visibility: visible;
      }
    }

    @if not (S./infix == "") {
      @include media-breakpoint-up(S./next) {
        --#{S./prefix}offcanvas-height: auto;
        --#{S./prefix}offcanvas-border-width: 0;
        background-color: transparent !important; // stylelint-disable-line declaration-no-important

        .offcanvas-header {
          display: none;
        }

        .offcanvas-body {
          display: flex;
          flex-grow: 0;
          padding: 0;
          overflow-y: visible;
          // Reset `background-color` in case `.bg-*` classes are used in offcanvas
          background-color: transparent !important; // stylelint-disable-line declaration-no-important
        }
      }
    }
  }
}

.offcanvas-backdrop {
  @include overlay-backdrop(S./zindex-offcanvas-backdrop, S./offcanvas-backdrop-bg, S./offcanvas-backdrop-opacity);
}

.offcanvas-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: var(--#{S./prefix}offcanvas-padding-y) var(--#{S./prefix}offcanvas-padding-x);

  .btn-close {
    padding: calc(var(--#{S./prefix}offcanvas-padding-y) * .5) calc(var(--#{S./prefix}offcanvas-padding-x) * .5);
    margin-top: calc(-.5 * var(--#{S./prefix}offcanvas-padding-y));
    margin-right: calc(-.5 * var(--#{S./prefix}offcanvas-padding-x));
    margin-bottom: calc(-.5 * var(--#{S./prefix}offcanvas-padding-y));
  }
}

.offcanvas-title {
  margin-bottom: 0;
  line-height: var(--#{S./prefix}offcanvas-title-line-height);
}

.offcanvas-body {
  flex-grow: 1;
  padding: var(--#{S./prefix}offcanvas-padding-y) var(--#{S./prefix}offcanvas-padding-x);
  overflow-y: auto;
}
