// Select
//
// Replaces the browser default select with a custom one, mostly pulled from
// https://primer.github.io/.

.form-select {
  --#{S./prefix}form-select-bg-img: #{escape-svg(S./form-select-indicator)};

  display: block;
  width: 100%;
  padding: S./form-select-padding-y S./form-select-indicator-padding S./form-select-padding-y S./form-select-padding-x;
  font-family: S./form-select-font-family;
  @include font-size(S./form-select-font-size);
  font-weight: S./form-select-font-weight;
  line-height: S./form-select-line-height;
  color: S./form-select-color;
  appearance: none;
  background-color: S./form-select-bg;
  background-image: var(--#{S./prefix}form-select-bg-img), var(--#{S./prefix}form-select-bg-icon, none);
  background-repeat: no-repeat;
  background-position: S./form-select-bg-position;
  background-size: S./form-select-bg-size;
  border: S./form-select-border-width solid S./form-select-border-color;
  @include border-radius(S./form-select-border-radius, 0);
  @include box-shadow(S./form-select-box-shadow);
  @include transition(S./form-select-transition);

  &:focus {
    border-color: S./form-select-focus-border-color;
    outline: 0;
    @if S./enable-shadows {
      @include box-shadow(S./form-select-box-shadow, S./form-select-focus-box-shadow);
    } @else {
      // Avoid using mixin so we can pass custom focus shadow properly
      box-shadow: S./form-select-focus-box-shadow;
    }
  }

  &[multiple],
  &[size]:not([size="1"]) {
    padding-right: S./form-select-padding-x;
    background-image: none;
  }

  &:disabled {
    color: S./form-select-disabled-color;
    background-color: S./form-select-disabled-bg;
    border-color: S./form-select-disabled-border-color;
  }

  // Remove outline from select box in FF
  &:-moz-focusring {
    color: transparent;
    text-shadow: 0 0 0 S./form-select-color;
  }
}

.form-select-sm {
  padding-top: S./form-select-padding-y-sm;
  padding-bottom: S./form-select-padding-y-sm;
  padding-left: S./form-select-padding-x-sm;
  @include font-size(S./form-select-font-size-sm);
  @include border-radius(S./form-select-border-radius-sm);
}

.form-select-lg {
  padding-top: S./form-select-padding-y-lg;
  padding-bottom: S./form-select-padding-y-lg;
  padding-left: S./form-select-padding-x-lg;
  @include font-size(S./form-select-font-size-lg);
  @include border-radius(S./form-select-border-radius-lg);
}

@if S./enable-dark-mode {
  @include color-mode(dark) {
    .form-select {
      --#{S./prefix}form-select-bg-img: #{escape-svg(S./form-select-indicator-dark)};
    }
  }
}
